.ReactModalPortal {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 30;
	width: 100%;
	height: 100%;
}

.modal-overlay {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 20;
	display: flex;
	width: 100%;
	height: 100%;
	justify-content: space-around;
	align-items: center;
	// stylelint-disable-next-line
	inset: 0;
	background: rgba(0, 0, 0, 0.85);
}

.modal {
	position: absolute;
	display: flex;
	padding: 2em;
	overflow: auto;
	font-family: Arial, sans-serif;
	color: #fff;
	background: #333c57;
	outline: none;

	@media only screen and (max-width: 799px) {
		// stylelint-disable-next-line
		h2,
		h3 {
			font-size: 2em;
		}
	}

	@media only screen and (min-width: 800px) {
		// stylelint-disable-next-line
		h2 {
			font-size: 4em;
		}

		// stylelint-disable-next-line
		h3 {
			font-size: 3em;
		}
	}

	// stylelint-disable-next-line
	h2 {
		padding: 0 1.5em;
		margin-bottom: 0.5em;
		text-align: center;
	}

	// stylelint-disable-next-line
	h3 {
		margin-bottom: 0.5em;
		text-align: center;
	}

	.highlight {
		font-weight: 700;
		color: $profile-player-name;
	}
}

.discord-link {
	margin-top: 1em;
	text-align: center;

	// stylelint-disable-next-line
	p {
		margin-bottom: 1em;
	}

	// stylelint-disable-next-line
	img {
		// stylelint-disable-next-line unit-disallowed-list
		width: 260px;
	}
}

.match-rewards-content {
	.health {
		margin-bottom: 0.5em;
		font-size: 2em;
		text-align: center;

		.highlight {
			color: #ff6464;
		}
	}

	.money {
		// stylelint-disable-next-line
		ul {
			font-size: 1.4em;
		}
		// stylelint-disable-next-line
		li {
			margin-bottom: 0.5em;
		}
	}
}

.ready-overlay-content {
	button {
		font-size: 1rem;
		background-color: rgb(26, 28, 44);
		border-style: none;
		color: white;
		padding: 1em;
		margin: auto;
	}
	.h2h-header {
		margin-bottom: 0.25em;
		font-size: 1.5em;
		text-align: center;
	}

	.inner-profile-box {
		display: flex;
		flex-direction: row;
	}

	.name-and-health {
		display: flex;
		flex-direction: column;
		font-family: Arial, sans-serif;
		text-transform: capitalize;

		&.right {
			justify-content: right;
			align-items: right;
			text-align: right;
		}

		.healthbar-container {
			width: 12em;
		}
	}

	.player-name {
		font-family: Arial, sans-serif;
		font-weight: 550;
		text-transform: uppercase;

		&.right {
			text-align: right;
		}
	}

	.spacer {
		width: 1em;
		height: 0.625em;
	}
	.emoji-spacer {
		width: 1em;
		height: 1em;
	}

	.quick-chat-button-container {
		display: flex;
		flex-direction: row;
		width: 100%;
		justify-content: center;

		.emoji-spacer {
			width: 1em;
			height: 1em;
		}
	}
}

.head-to-head-stats {
	.h2h-stat-div {
		margin-bottom: 0.5em;
		text-align: center;
	}

	.h2h-info-header {
		margin-bottom: 0.25em;
		font-size: 0.7em;
	}

	.h2h-info-text {
		font-size: 1.125em;
	}
}

.victory-overlay {
	// stylelint-disable-next-line
	h2 {
		font-weight: 700;
		text-transform: uppercase;
	}

	.highlight {
		font-style: italic;
	}

	.winner {
		font-size: 2em;
		font-weight: 700;
		text-align: center;
	}

	.game-over {
		font-size: 1.8em;
		font-weight: 700;
		text-transform: uppercase;
	}

	.spectate {
		font-size: 1.2em;
	}
}

.reconnect-overlay {
	width: 100%;
	height: 100%;
	text-align: center;

	.text {
		margin-bottom: 1em;
		font-family: Arial, Helvetica, sans-serif;
		font-size: 1.5em;
		color: #fff;
	}
}

.spectating-overlay {
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	text-align: center;
	background-color: #566c86;
	color: white;
	padding-bottom: 1.25rem;
	font-family: Arial, Helvetica, sans-serif;
	h1 {
		font-size: 1.25rem;
		padding-bottom: 0.25rem;
		padding-top: 0.125rem;
		text-transform: capitalize;
	}

	p {
		font-size: 1.75rem;
		font-weight: bold;
		padding-bottom: 0.25rem;
	}

	button {
		font-size: 1rem;
		background-color: rgb(26, 28, 44);
		border-style: none;
		color: white;
		padding: 1em;
		margin: auto;
	}

	@media screen and (max-width: 900px) {
		h1 {
			font-size: 0.75rem;
		}

		p {
			font-size: 1rem;
		}

		button {
			font-size: 0.75rem;
		}
	}
	@media screen and (max-width: 500px) {
		h1 {
			font-size: 0.5rem;
		}

		p {
			font-size: 0.75rem;
		}

		button {
			font-size: 0.5rem;
		}
	}
}
